import React from 'react'
import { render } from 'react-dom'
import { BrowserRouter } from 'react-router-dom'
import App from './App';

render((
  <BrowserRouter>
     <App loggedInManager={document.getElementById('managername').innerHTML } />
  </BrowserRouter>
), document.getElementById('root'));



